// import ReactHtmlParser from 'react-html-parser';
import { get, getFile } from './apiUtil';
const md = require('markdown-it')();

function loadDirFromApi(api) {
    const data = new Promise((resolve, reject) => {
        get(api).then(data => {
            return resolve(data);
        })
    })
    return data;
}

function loadFile(api) {
    const data = new Promise((resolve, reject) => {
        getFile(api).then(data => {
            return resolve(data);
        });
    })
    return data;
}

function renderMDFile(data) {
    return <div dangerouslySetInnerHTML={{ __html: md.render(data) }} />;
}

export {
    loadDirFromApi,
    loadFile,
    renderMDFile
}